import SourceState from "ol/source/State";
// import {
//     DEFAULT_TILE_SIZE
// } from "ol/tilegrid/common";

import { AttributionLike } from "ol/source/Source";
import TileImage from "ol/source/TileImage";

import { Reader } from "./Reader";

export type RegionalProps = {
    urls?: string[];
    files?: File[];
    minZoom?: number;
    maxZoom?: number;
    tileSize?: number;
    attributions?: AttributionLike;
    attributionsCollapsible?: boolean;
    cacheSize?: number;
    crossOrigin?: string | null;
    interpolate?: boolean;
    opaque?: boolean;
    reprojectionErrorThreshold?: number;
    tilePixelRatio?: number;
    wrapX?: boolean;
    transition?: number;
};

export class Regional extends TileImage {
    constructor(userOptions: RegionalProps) {
        const options = userOptions || {};

        // const tileSize = options.tileSize ? options.tileSize : DEFAULT_TILE_SIZE;

        super({
            state: SourceState.LOADING,
            attributions: options.attributions,
            cacheSize: options.cacheSize,
            crossOrigin: options.crossOrigin,
            // interpolate: interpolate,
            opaque: options.opaque,
            // projection,
            reprojectionErrorThreshold: options.reprojectionErrorThreshold,
            // tileLoadFunction,
            tilePixelRatio: options.tilePixelRatio,
            url: "{z},{x},{y}",
            wrapX: options.wrapX !== undefined ? options.wrapX : true,
            transition: options.transition,
            attributionsCollapsible: options.attributionsCollapsible,
        });

        (async () => {
            const a = new Reader({ files: options.files });
            console.log(await a.getCode())
            console.log(await a.render({
                mode: "single",
                bands: [{
                    order: 1,
                    min: 0,
                    max: 1000
                }],
                sources: [
                    {
                        fileIndex: 0,
                        bands: [1, 3],
                        // window:[100,100,1300,1300],
                        width: 10,
                        height: 10,
                        nodata: 0,
                    }]
            }))
        })();
    }
}